<template>
    <div ref="chart" class="d3-chart"></div>
</template>

<script>
import * as d3 from 'd3';

export default {
    props: {
        data: Array
    },
    data() {
        return {
            svg: null,
            x: null,
            y: null,
            width: window.innerWidth*.9,
            height: (window.innerHeight-200)*.9,
            lineConfigurations: [],
        };
    },
    watch: {
        data(newData) {
            this.updateChart(newData);
        }
    },
    mounted() {
        this.createChart();
    },
    methods: {
        createChart() {
            const margin = { top: 10, right: 30, bottom: 30, left: 60 };
            this.svg = d3.select(this.$refs.chart)
                .append('svg')
                .attr('width', this.width + margin.left + margin.right)
                .attr('height', this.height + margin.top + margin.bottom)
                .append('g')
                .attr('transform', `translate(${margin.left},${margin.top})`);

            this.x = d3.scaleTime().range([0, this.width]);
            this.y = d3.scaleLinear().range([this.height, 0]);

            this.svg.append("g").attr("class", "x-axis")
                .attr("transform", `translate(0,${this.height})`);
            this.svg.append("g").attr("class", "y-axis");

            this.yDensity = d3.scaleLinear().range([this.height, 0]);

            this.svg.append("g")
                .attr("class", "y-axis-density")
                .attr("transform", `translate(${this.width}, 0)`); // Position this axis on the right


        },
        updateChart(data) {
            if (!data) data = [];
            const normalizedData = this.normalizeData(data.marketData); // Assuming marketData is part of the data prop
            const tradeData = this.processTradeData(this.normalizeData(data.marketTrades)); // Assuming marketTrades is part of the data prop
            const trendlines = this.calculateTrendlines(normalizedData, normalizedData.length/40);
            // Reset the line configurations
            this.lineConfigurations = [
                {
                    data: this.toPrice(normalizedData),
                    color: 'steelblue',
                    className: '.price-chart',
                    strokeWidth: 3
                },
                {
                    data: this.calculateEMA(normalizedData, 150),
                    color: 'orange',
                    className: '.ema-chart-short',
                    strokeWidth: 1.25
                },
                {
                    data: this.calculateEMA(normalizedData, 450),
                    color: 'purple',
                    className: '.ema-chart-long',
                    strokeWidth: 1.25
                },
                {
                    data: this.calculateEMA(normalizedData, 1200),
                    color: 'blue',
                    className: '.ema-chart-very-long',
                    strokeWidth: 1.25
                },
                {
                    data: trendlines.supportPoints,
                    color: 'green',
                    className: '.support',
                    strokeWidth: 2
                },
                {
                    data: trendlines.resistancePoints,
                    color: 'red',
                    className: '.resistance',
                    strokeWidth: 2
                },
            ];

            // Add to updateChart method within methods:
            // Calculate the trade density data once
            const tradeDensityData = this.calculateTradeDensity(normalizedData, 10);

            // Proceed to add trade density to the line configurations
            this.lineConfigurations.push({
                data: tradeDensityData,
                color: 'black',
                className: '.density-chart'
            });

            // Draw trade density line
            this.drawTradeDensityLines(tradeDensityData);

            // Combine all datasets for domain calculation
            this.combinedDataForDomain = this.lineConfigurations.reduce((acc, config) => acc.concat(config.data), []);

            // Finalize registration by setting scale domains and updating axes
            this.finalizeRegistration(normalizedData);

            // Draw lines based on the stored configurations
            this.drawAllLines();

            this.drawTrades(tradeData);
        },
        processTradeData(tradeData) {
            // Processing trade data to a suitable format for chart
            if(tradeData && tradeData.length > 0)
            return tradeData.map(trade => ({
                ...trade,
                tradedOn: trade.tradedOn, // Convert to Date object if necessary
                yValue: trade.price
            }));
        },
        finalizeRegistration(normalizedData) {
            // Set the domain for the x-axis based on the dates in your data
            this.x.domain(d3.extent(normalizedData, d => d.tradedOn));

            // Set the domain for the y-axis based on the price values in your data
            this.y.domain([
                d3.min(normalizedData, d => d.price),
                d3.max(normalizedData, d => d.price)
            ]);

            // Update the x and y axes
            this.svg.select(".x-axis").transition().call(d3.axisBottom(this.x));
            this.svg.select(".y-axis").transition().call(d3.axisLeft(this.y));

            // The yDensity domain is set within the drawTradeDensityLine method
        },

        drawAllLines() {
            // Loop over each line configuration and draw it
            this.lineConfigurations.forEach(config => {
                this.updateLine(config.data, config.color, config.className, config.strokeWidth);
            });
        },


        updateAxes() {
            this.svg.select(".x-axis").call(d3.axisBottom(this.x));
            this.svg.select(".y-axis").call(d3.axisLeft(this.y));
        },
        // registerLine(data, color, className, strokeWidth) {
        //     // Add the current line's data to the combined data array
        //     this.combinedDataForDomain = this.combinedDataForDomain.concat(data);

        //     // Call updateLine with the already transformed data
        //     this.updateLine(data, color, className, strokeWidth);
        // },

        drawTrades(tradeData) {
            if(!tradeData)
                return;
            const tradeMarkerSize = 5;
            const stopLossMarkerSize = 3;  // Smaller size for stop loss markers
            const takeProfitMarkerSize = 3;  // Smaller size for stop loss markers

            // Remove any existing trade and stop loss markers
            this.svg.selectAll(".trade-marker, .stop-loss-marker, .take-profit-marker").remove();

            // Bind trade data to circles (trade markers)
            this.svg.selectAll(".trade-marker")
                .data(tradeData)
                .enter()
                .append('circle')
                    .attr('class', 'trade-marker')
                    .attr('cx', d => this.x(d.tradedOn))
                    .attr('cy', d => this.y(d.yValue))
                    .attr('r', tradeMarkerSize)
                    .attr('fill', d => d.isBuy ? 'green' : 'red'); // Green for buy, red for sell

            this.svg.selectAll(".stop-loss-marker")
                .data(tradeData)
                .enter()
                .append('circle')
                    .attr('class', 'stop-loss-marker')
                    .attr('cx', d => this.x(d.tradedOn))
                    .attr('cy', d => this.y(d.stopLoss)) // Using stopLoss value for y position
                    .attr('r', stopLossMarkerSize)
                    .attr('fill', 'red'); // Different color for stop loss markers
            this.svg.selectAll(".take-profit-marker")
                .data(tradeData)
                .enter()
                .append('circle')
                    .attr('class', 'take-profit-marker')
                    .attr('cx', d => this.x(d.tradedOn))
                    .attr('cy', d => this.y(d.takeProfit)) // Using stopLoss value for y position
                    .attr('r', takeProfitMarkerSize)
                    .attr('fill', 'green'); // Different color for stop loss markers
        },




        updateLine(data, color, className, strokeWidth, yScale = this.y) {
            const line = d3.line()
                .x(d => this.x(d.tradedOn))
                .y(d => yScale(d.yValue)) // Use the specified yScale here
                .defined(d => d.yValue !== null && d.yValue !== undefined);


            // Remove any existing path with the same class name
            this.svg.selectAll(className).remove();

            // Append the new path
            this.svg.append('path')
                .data([data])
                .attr('class', className.slice(1))
                .attr('fill', 'none')
                .attr('stroke', color)
                .attr('stroke-width', strokeWidth)
                .attr('d', line);
        },
        normalizeData(data) {
            return data.map(d => {
                const parsedDate = d3.isoParse(d.tradedOn);
                var ret = { ...d, tradedOn: parsedDate };
                return ret;
            });
        },


        toPrice(data) {
            return data.map(d => ({ tradedOn: d.tradedOn, yValue: d.price }));
        },
        toDensity(densityData) {
            return densityData.map(d => ({
                tradedOn: d.tradedOn,
                yValue: d.tradeCount // This will be the density value
            }));
        },

        calculateEMA(data, period = 10) {
            let ema = [];
            let multiplier = 2 / (period + 1);

            // Assuming 'data' is an array of { tradedOn, price } objects
            for (let i = 0; i < data.length; i++) {
                if (i < period - 1) {
                    ema.push(null); // Not enough data to calculate EMA
                    continue;
                }
                if (i === period - 1) {
                    // Simple average of first 'period' prices
                    let sum = 0;
                    for (let j = 0; j < period; j++) {
                        sum += data[j].price;
                    }
                    ema.push(sum / period);
                } else {
                    // Calculate EMA
                    ema.push((data[i].price - ema[i - 1]) * multiplier + ema[i - 1]);
                }
            }
            return data.map((d, index) => ({
                tradedOn: d.tradedOn,
                yValue: ema[index] // Use a generic property name
            }));
        },
        calculateTrendlines(normalizedData, windowSize = 25) {
            const supportPoints = [];
            const resistancePoints = [];
            
            windowSize = parseInt(windowSize);
            
            for (let i = windowSize; i < normalizedData.length - windowSize; i++) {
                let isSupport = true;
                let isResistance = true;
                if(!normalizedData[i])
                    continue;
                const currentPrice = normalizedData[i].price;

                for (let j = -windowSize; j <= windowSize; j++) {
                    if (j === 0) continue;
                    if(!normalizedData[i + j])
                        continue;
                    else { 
                        const comparisonPrice = normalizedData[i + j].price;
                        if (currentPrice > comparisonPrice) {
                            isSupport = false;
                        }
                        if (currentPrice < comparisonPrice) {
                            isResistance = false;
                        }
                    }

                    // Early break if neither support nor resistance
                    if (!isSupport && !isResistance) break;
                }

                if (isSupport) {
                    supportPoints.push({ tradedOn: normalizedData[i].tradedOn, yValue: currentPrice });
                }
                if (isResistance) {
                    resistancePoints.push({ tradedOn: normalizedData[i].tradedOn, yValue: currentPrice });
                }
            }

            return { supportPoints, resistancePoints };
        },
        calculateTradeDensity(normalizedData, densityInterval) {
            const densityData = [];
            // Convert densityInterval from minutes to milliseconds for comparison
            const intervalMs = densityInterval * 60 * 1000;

            normalizedData.forEach((currentTrade, index) => {
                // Determine the start of the interval for the current trade
                let intervalStart = new Date(currentTrade.tradedOn.getTime() - intervalMs);

                // Initialize counts for buy and sell trades
                let buyTradeCount = 0;
                let sellTradeCount = 0;

                normalizedData.slice(0, index + 1).forEach(trade => {
                    if (trade.tradedOn >= intervalStart && trade.tradedOn <= currentTrade.tradedOn) {
                        if (trade.isBuy) {
                            buyTradeCount++;
                        } else {
                            sellTradeCount++;
                        }
                    }
                });

                // Add the trade counts for the current interval to the density data array
                densityData.push({ 
                    tradedOn: currentTrade.tradedOn, 
                    buyTradeCount: buyTradeCount,
                    sellTradeCount: sellTradeCount 
                });
            });

            return densityData;
        },
        drawTradeDensityLines(densityData) {
            // Calculate the maximum trade count for the yDensity domain
            const maxDensity = d3.max(densityData, d => Math.max(d.buyTradeCount, d.sellTradeCount));
            this.yDensity.domain([0, maxDensity]);

            // Update the secondary y-axis
            this.svg.select(".y-axis-density").transition().call(d3.axisRight(this.yDensity));


            const buyLine = d3.line()
                .defined(d => !isNaN(d.buyTradeCount))
                .x(d => this.x(d.tradedOn))
                .y(d => this.yDensity(d.buyTradeCount))
                //.curve(d3.curveMonotoneX);

            const sellLine = d3.line()
                .defined(d => !isNaN(d.sellTradeCount))
                .x(d => this.x(d.tradedOn))
                .y(d => this.yDensity(d.sellTradeCount))
                //.curve(d3.curveMonotoneX);

            // Check the generated path data for debugging
            // console.log("Buy Line Path Data:", buyLine(densityData));
            // console.log("Sell Line Path Data:", sellLine(densityData));

            // Draw buy density line
            this.svg.selectAll('.buy-density-line').remove(); // Clear any existing line
            this.svg.append('path')
                .datum(densityData)
                .attr('class', 'buy-density-line')
                .attr('stroke', 'green')
                .attr('stroke-width', .75)
                .attr('fill', 'none')
                .attr('d', buyLine);

            // Draw sell density line
            this.svg.selectAll('.sell-density-line').remove(); // Clear any existing line
            this.svg.append('path')
                .datum(densityData)
                .attr('class', 'sell-density-line')
                .attr('stroke', 'red')
                .attr('stroke-width', .75)
                .attr('fill', 'none')
                .attr('d', sellLine);
        },


    }
};
</script>

