<template>
  <div>
    <!-- Dropdown for symbol selection -->
    <select v-model="selectedSymbol">
      <option value="BTC_USDT">BTC_USDT</option>
      <option value="SOL_USDT">SOL_USDT</option>
      <option value="GALA_USDT">GALA_USDT</option>
      <option value="DOGE_USDT">DOGE_USDT</option>
    </select>

    <!-- Date range inputs -->
    <input type="datetime-local" v-model="fromDate"/>
    <input type="datetime-local" v-model="toDate" v-if="!autoRefreshEnabled"/>

    <!-- Checkbox for enabling auto-refresh -->
    <label>
      <input type="checkbox" v-model="autoRefreshEnabled"> Enable Auto Refresh
    </label>

    <button @click="fetchMarketData" v-if="!autoRefreshEnabled">Load Data</button>

    <!-- Chart component -->
    <BasicChart :data="{ marketData, marketTrades }"/>
  </div>
</template>

<script>
import BasicChart from './BasicChart.vue';
import axios from 'axios';

export default {
  components: {
    BasicChart
  },
  data() {
    return {
      selectedSymbol: 'BTC_USDT',
      toDate: this.formatDateToLocalISOString(this.subtractMinutes(new Date(), -5)),
      fromDate: this.formatDateToLocalISOString(this.subtractMinutes(new Date(), 60)),
      marketData: [],
      marketTrades: [],
      autoRefreshEnabled: true,
      autoRefreshInterval: null
    };
  },
  watch: {
    autoRefreshEnabled(newVal) {
      if (newVal) {
        this.startAutoRefresh();
      } else {
        this.stopAutoRefresh();
      }
    }
  },
  mounted() {
    if(this.autoRefreshEnabled) { 
      this.startAutoRefresh();
    }
    this.setChartHeight();
  },
  methods: {
    setChartHeight() {
      this.width = 1000;
      this.height = 800;
      // Update chart size here if necessary
      if (this.svg) {
        // Update svg height
        this.svg.attr('height', this.height);
        this.svg.attr('width', this.width);
        // Additional code to update the chart...
      }
    },
    async fetchMarketData() {
      try {
        this.setChartHeight();

        // Fetching market data
        const marketResponse = await axios.get(`/api/Market/getMarketForSymbol/${this.selectedSymbol}/${this.fromDate}/${this.toDate}`);
        this.marketData = marketResponse.data;
        
        // Fetching trade data
        const tradeResponse = await axios.get(`/api/TradeBroker/GetTradesBetween/${this.selectedSymbol}/${this.fromDate}/${this.toDate}`);
        this.marketTrades = tradeResponse.data;

      } catch (error) {
        console.error('Error fetching data:', error);
      }
    },  
    startAutoRefresh() {
      this.autoRefreshInterval = setInterval(() => {
        this.toDate = this.formatDateToLocalISOString(this.subtractMinutes(new Date(), -5));
        this.fetchMarketData();
      }, 2000);
    },
    stopAutoRefresh() {
      clearInterval(this.autoRefreshInterval);
    },
    subtractMinutes(date, minutes) {
      return new Date(date.getTime() - minutes * 60000);
    },
    formatDateToLocalISOString(date) {
      const offset = date.getTimezoneOffset();
      const localDate = new Date(date.getTime() - offset * 60 * 1000);
      return localDate.toISOString().slice(0, 16);
    }
  },
  beforeUnmount() {
    // Ensure the interval is cleared when the component is destroyed
    this.stopAutoRefresh();
    window.removeEventListener('resize', this.setChartHeight);
  }
};
</script>

<style scoped>
.userdashboard {
  width: 100%;
  min-height: 100%;
  padding: 20px;
  background-color: #f4f4f4; /* Light gray background */
  box-sizing: border-box;
}
</style>
